.pageWrapper {
  width: 100%;
  height: 100%;
  background-color: white !important;
  overflow: hidden;
}
.rowDiv {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.columnDiv {
  display: flex;
  flex-direction: column;
}
.loading-container {
  width: 100%;
}
.loading-container .ant-spin-spinning {
  width: 100%;
}
